(function() {
    'use strict';

    angular
        .module('plingUiLite')
        .service('cepService', CepService);

    CepService.$inject = [ 'httpService' ];

    function CepService(httpService) {

        this.getCep = function (cep) {
            return httpService.get('smart', 'cep', cep);
        };
    }

}());