(function () {
    'use strict';

    angular
        .module('plingUiLite')
        .factory('shadowLogger', PlingUiLogger);

    function PlingUiLogger() {

        return function ($delegate) {

            return {

                'log': function () {
                    this.dispatch('log', arguments);
                },

                'info': function () {
                    this.dispatch('info', arguments);
                },

                'error': function () {
                    this.dispatch('error', arguments);
                },

                'warn': function () {
                    this.dispatch('warn', arguments);
                },

                'dispatch': function (method, params) {
                    // defining method
                    var proc = $delegate[method] || $delegate.log,
                        stamp = new Date().toString(),
                        prefix = '[' + stamp + '][' + method + ']::',
                        msg = [],
                        arg;

                    if (method) {
                        // preparing msg
                        msg.push(prefix);

                        // joining params
                        for (arg in params) {
                            if (params.hasOwnProperty(arg)) {
                                msg.push(params[arg]);
                            }
                        }

                        // applying log info
                        proc.apply(null, msg);
                    }
                }
            };
        };
    }
}());